import React,{useState,useEffect} from "react";
import {
    Button,
    Card,
    Input,
    Row,
    Col,
    Table
} from "antd";
import {Api,baseURL} from "../api";
import qs from "qs";
const id = localStorage.getItem("userid");
const token = localStorage.getItem("token");
const address_id = localStorage.getItem("address_id");
function DriverOnDuty(){
    const [tableData,setTableData] = useState({});
    const [postData,setPostData] = useState({
        id,
        token,
        address_id,
        page:1
    });
    const initData = {
        id,
        token,
        address_id,
        search:"",
        page:1
    };
    useEffect(()=>{
        getDriverOnDuty(initData);
        console.log(tableData);
        return ()=>{};
    },[]);
    async function getDriverOnDuty(Data = {...postData}){
        try{
            const response = await Api.post("/admin/Analysis/driver_onlinetime",qs.stringify(Data));
            console.log(response);
            response.data.error_code === 0 && setTableData(response.data);
        }catch(error){
            console.error(error)
        }
    }

    const columns = [
        {
          title:"",
            dataIndex:'card'
        },
        {
            title:"",
            dataIndex:"name"
        },
        {
            title:"",
            dataIndex:"phone"
        },
        {
            title:"",
            dataIndex:"dutyTime"
        },
        {
            title:"",
            dataIndex:"action"
        }
    ];
    return (
        <div>
            <Card>
                <Row gutter={1} row-flex={'center'}>
                    <Col span={6}>
                        <Input
                            placeholder={''}
                        />
                    </Col>
                    <Col span={2} offset={1}>
                       
                    </Col>
                    <Col span={3}>
                        <Input
                            placeholder={''}
                        />
                    </Col>
                    <Col span={2} offset={1}>
                        
                    </Col>
                    <Col span={3}>
                        <Input
                            placeholder={''}
                        />
                    </Col>
                    <Col span={1}>
                        <Button onClick={()=>{
                            getDriverOnDuty()
                        }}></Button>
                    </Col>
                    <Col span={1} offset={1}>
                        <Button></Button>
                    </Col>
                    <Col span={1} offset={1}>
                        <Button></Button>
                    </Col>
                </Row>
            </Card>
            <Card>
                <Table
                    columns={columns}
                    dataSource={tableData.list}
                />
            </Card>
        </div>
    )
}
export default DriverOnDuty;